const uni = require('uni').default;















import pageHead from '../../../components/page-head.vue'

const requestUrl = "https://service.dcloud.net.cn/ajax/echo/text?name=uni-app"
const duration = 2000
export default {
	data() {
		return {
			title: 'request',
			loading: false
		}
	},
	methods: {
		makeRequest: function () {
			this.loading = true
			uni.request({
				url: requestUrl,
				data: {
					noncestr: Date.now()
				},
				success: (res) => {
					uni.showToast({
						title: '请求成功',
						icon: 'success',
						mask: true,
						duration: duration
					})
					console.log('request success', res)
				},
				fail: (err) => {
					console.log('request fail', err);
					uni.showModal({
						content:err.errMsg,
						showCancel:false
					})
				},
				complete:()=> {
					this.loading = false
				}
			})
		}
	},
	components: {
		pageHead

	}
}



